import { createSlice } from "@reduxjs/toolkit";
import { addProductBasket, delProductBasket, resetProductBasket } from "../actions/productBasketActions";
const initialState = {};
const productReducer = createSlice({
    name: 'productsBasket',
    initialState,
    reducers: {},
    extraReducers: builder => {
        builder
            .addCase(addProductBasket, (state, action) => {
            if (action.payload in state) {
                const countProduct = state[action.payload];
                state[action.payload] = countProduct + 1;
            }
            state[action.payload] = 1;
        })
            .addCase(delProductBasket, (state, action) => {
            if (action.payload in state) {
                const countProduct = state[action.payload];
                const filterCount = countProduct < 1 ? 0 : countProduct - 1;
                state[action.payload] = filterCount;
            }
        })
            .addCase(resetProductBasket, () => {
            return initialState;
        });
    }
});
export default productReducer.reducer;
